import React from 'react';

export default function StaffTimetableGrid({ timetable, staffName }) {
    const days = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday'];
    const periods = ['P1', 'P2', 'P3', 'P4', 'P5', 'P6'];

    // Get the specific staff's timetable
    const staffSchedule = timetable[staffName];

    // Debug log
    console.log('StaffTimetableGrid - Staff name:', staffName);
    console.log('StaffTimetableGrid - Staff schedule:', staffSchedule);
    console.log('StaffTimetableGrid - All timetables:', Object.keys(timetable));

    if (!staffSchedule) {
        return (
            <div style={{ padding: '40px', textAlign: 'center' }}>
                <div style={{ fontSize: '48px', marginBottom: '20px' }}>‚ö†Ô∏è</div>
                <h3>No Timetable Found</h3>
                <p style={{ color: '#64748b' }}>
                    No timetable found for "{staffName}".
                </p>
                <p style={{ color: '#64748b', marginTop: '10px', fontSize: '14px' }}>
                    Available staff: {Object.keys(timetable).join(', ')}
                </p>
            </div>
        );
    }

    return (
        <div className="timetable-container" id="staff-timetable-export">
            <table className="timetable">
                <thead>
                    <tr>
                        <th className="day-header">Day/Period</th>
                        {periods.map(period => (
                            <th key={period} className="period-header">{period}</th>
                        ))}
                    </tr>
                </thead>
                <tbody>
                    {days.map((day, dayIndex) => (
                        <tr key={day}>
                            <td className="day-cell">{day}</td>
                            {periods.map((period, periodIndex) => {
                                const slot = staffSchedule[dayIndex]?.[periodIndex];
                                const isFree = !slot || slot.type === 'free' || slot.subject === 'FREE';

                                return (
                                    <td 
                                        key={period} 
                                        className={`period-cell ${isFree ? 'free-period' : 'class-period'}`}
                                    >
                                        {isFree ? (
                                            <div className="period-content free">
                                                <span className="subject-name">FREE</span>
                                            </div>
                                        ) : (
                                            <div className="period-content">
                                                <span className="subject-name">{slot.subject}</span>
                                                <span className="class-name">{slot.class}</span>
                                            </div>
                                        )}
                                    </td>
                                );
                            })}
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
}